//获取试卷并跳转到添加题库
<template>
  <div class="exam">
    <el-table :data="pagination.records" border>
      <el-table-column fixed="left" label="考试科目" prop="source" width="120"></el-table-column>
      <el-table-column label="考试描述" prop="type" width="180"></el-table-column>
      <!-- <el-table-column
        prop="description"
        label="介绍"
        width="200"
      ></el-table-column> -->
      <!-- <el-table-column
        prop="institute"
        label="所属学院"
        width="180"
      ></el-table-column> -->
      <el-table-column label="所属专业" prop="major" width="200"></el-table-column>
      <el-table-column label="年级" prop="grade" width="100"></el-table-column>
      <el-table-column label="考试日期" prop="examDate" width="120"></el-table-column>
      <el-table-column label="持续时间" prop="totalTime" width="120"></el-table-column>
      <el-table-column label="总分" prop="totalScore" width="120"></el-table-column>
      <el-table-column label="考生须知" prop="tips" width="400"></el-table-column>
      <el-table-column fixed="right" label="操作" width="150">
        <template slot-scope="scope">
          <el-button
            size="small"
            type="primary"
            @click="add(scope.row.paperId, scope.row.source)"
          >添加试题
          </el-button
          >
        </template>
      </el-table-column>
    </el-table>
    <el-pagination
      :current-page="pagination.current"
      :page-size="pagination.size"
      :page-sizes="[4, 8, 10, 20]"
      :total="pagination.total"
      class="page"
      layout="total, sizes, prev, pager, next, jumper"
      @size-change="handleSizeChange"
      @current-change="handleCurrentChange"
    >
    </el-pagination>
  </div>
</template>

<script>
export default {
  data() {
    return {
      form: {}, //保存点击以后当前试卷的信息
      pagination: {
        //分页后的考试信息
        current: 1, //当前页
        total: null, //记录条数
        size: 4 //每页条数
      }
    };
  },
  created() {
    this.getExamInfo();
  },
  methods: {
    getExamInfo() {
      //分页查询所有试卷信息
      this.$axios(
        `/api/exams/${this.pagination.current}/${this.pagination.size}`
      )
        .then(res => {
          this.pagination = res.data.data;
        })
        .catch(error => {
        });
    },
    //改变当前记录条数
    handleSizeChange(val) {
      this.pagination.size = val;
      this.getExamInfo();
    },
    //改变当前页码，重新发送请求
    handleCurrentChange(val) {
      this.pagination.current = val;
      this.getExamInfo();
    },
    add(paperId, source) {
      //增加题库
      this.$router.push({
        path: "/addAnswerChildren",
        query: {paperId: paperId, subject: source}
      });
    }
  }
};
</script>
<style lang="scss" scoped>
.exam {
  padding: 0px 40px;

  .page {
    margin-top: 20px;
    display: flex;
    justify-content: center;
    align-items: center;
  }

  .edit {
    margin-left: 20px;
  }
}

/deep/ .cell {
  text-align: center;
}
</style>



